import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { setStyleStatusBar } from 'src/shared/utils/function.utils';

@Component({
  selector: 'app-onboarding-beyond.page',
  templateUrl: './onboarding-beyond.page.html',
  styleUrls: ['./onboarding-beyond.page.scss'],
})
export class OnboardingBeyondPage {
  constructor(
    private readonly router: Router
  ) { }

  ionViewDidEnter() {
    setStyleStatusBar("dark")
  }
  onRedirectToMain() {
    this.router.navigateByUrl('/main', { replaceUrl: true })
  }
}
